/*add fonts*/
@font-face { font-family: 'Open Sans Condensed Bold'; src: url("../fonts/OpenSansCondensed-Bold.woff2") format("woff2"), url("../fonts/OpenSansCondensed-Bold.woff") format("woff"); font-weight: bold; font-style: normal; font-display: swap; }

@font-face { font-family: 'Open Sans Condensed Light'; src: url("../fonts/OpenSansCondensed-Light.woff2") format("woff2"), url("../fonts/OpenSansCondensed-Light.woff") format("woff"); font-weight: 300; font-style: normal; font-display: swap; }

@font-face { font-family: 'Open Sans Condensed Light Italic'; src: url("../fonts/OpenSansCondensed-LightItalic.woff2") format("woff2"), url("../fonts/OpenSansCondensed-LightItalic.woff") format("woff"); font-weight: 300; font-style: italic; font-display: swap; }

:root { --r-premium: #faa74a; --black: rgba(0, 0, 0, 0.87); --blacked: #323232; --danger: #ea3d2f; --ok: #2fa84f; --caution: #f8cb2b; --accent: #78909c; --action: #367bf5; --b-gblue: #d8dafb; --gblue: #dbdcee; --grey-black: #5e6366; --grey-light: #c6cacc; --grey-dark: #919699; --pale-str: #e3e5e5; --pale: #f7f9fa; --white: #fff; --bluer: #eef5ff; --q-xxl: 1400px; --q-xl: 1200px; --q-l: 992px; --q-m: 768px; --q-s: 576px; --xxl: 1320px; --xl: 1140px; --l: 960px; --m: 720px; --s: 540px; --fz: 14px; --os: "Open Sans", Arial, Helvetica, sans-serif; --osc-bold: "Open Sans Condensed Bold", Arial, Helvetica, sans-serif; --osc-light: "Open Sans Condensed Light", Arial, Helvetica, sans-serif; --osc-light-i: "Open Sans Condensed Light Italic", Arial, Helvetica,
    sans-serif; --transition-ease: all 500ms ease-out; --font: 400 var(--fz) / 1.33 var(--os); }

html[data-theme="light"] { --white: #fff; --card-color: #eef5ff; }

html[data-theme="dark"] { --background-color: #212a2e; --text-color: #f7f8f8; --link-color: #828fff; --card-color: rgba(94, 99, 102, 0.35); }

/*style normalize*/
html { font-family: sans-serif; -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; }

body { margin: 0; height: 100%; }

article, aside, details, figcaption, figure, footer, header, hgroup, main, menu, nav, section, summary { display: block; }

audio, canvas, progress, video { display: inline-block; vertical-align: baseline; }

audio:not([controls]) { display: none; height: 0; }

[hidden], template { display: none; }

a { background-color: transparent; }

a:active, a:hover { outline: 0; }

abbr[title] { border-bottom: 1px dotted; }

b, strong { font-weight: bold; }

dfn { font-style: italic; }

h1 { font-size: 2em; }

mark { background: #ff0; color: #000; }

small { font-size: 80%; }

sub, sup { font-size: 75%; line-height: 0; position: relative; vertical-align: baseline; }

sup { top: -0.5em; }

sub { bottom: -0.25em; }

img { border: 0; }

svg:not(:root) { overflow: hidden; }

figure { margin: 1em 40px; }

hr { -moz-box-sizing: content-box; box-sizing: content-box; height: 0; }

pre { overflow: auto; }

code, kbd, pre, samp { font-family: monospace, monospace; font-size: 1em; }

button, input, optgroup, select, textarea { color: inherit; font: inherit; margin: 0; }

button { overflow: visible; }

button, select { text-transform: none; }

button, html input[type="button"], input[type="reset"], input[type="submit"] { -webkit-appearance: button; cursor: pointer; }

button[disabled], html input[disabled] { cursor: default; }

button::-moz-focus-inner, input::-moz-focus-inner { border: 0; padding: 0; }

input::-webkit-input-placeholder { color: #fff; font-size: 18px; }

input:hover::-webkit-input-placeholder { opacity: 0.5; }

input:hover::-moz-placeholder { opacity: 0.5; }

input:hover:-moz-placeholder { opacity: 0.5; }

input:hover:-ms-input-placeholder { opacity: 0.5; }

input { line-height: normal; opacity: 0.5; }

input:valid { opacity: 1; }

input:hover { opacity: 1; }

input:focus { opacity: 1; }

input:active #get-discount { opacity: 0.5; }

input[type="checkbox"], input[type="radio"] { box-sizing: border-box; padding: 0; }

input[type="number"]::-webkit-inner-spin-button, input[type="number"]::-webkit-outer-spin-button { height: auto; }

input[type="search"] { -webkit-appearance: textfield; -moz-box-sizing: content-box; -webkit-box-sizing: content-box; box-sizing: content-box; }

input[type="search"]::-webkit-search-cancel-button, input[type="search"]::-webkit-search-decoration { -webkit-appearance: none; }

fieldset { border: 1px solid #c0c0c0; margin: 0 2px; padding: 0.35em 0.625em 0.75em; }

legend { border: 0; padding: 0; }

textarea { overflow: auto; }

optgroup { font-weight: bold; }

table { border-collapse: collapse; border-spacing: 0; }

td, th { padding: 0; }

ul { margin: 0; list-style-type: none; padding: 0; }

p { margin: 0; }

html { height: 100%; }

html input[type="button"] { cursor: pointer; }

html input[disabled] { cursor: default; }

body { font: var(--font); margin: 0; color: var(--grey-black); min-width: var(--s); min-height: 100%; background: var(--white); }

a { color: var(--action); text-decoration: none; }

a:hover { text-decoration: underline; }

p { margin: 0 0 8px; }

img { border: none; max-width: 100%; vertical-align: top; }

h1, h2, h3 { margin: 0 0 4px; text-align: left; font-weight: 500; line-height: 1.2; font-family: var(--osc-bold); font-size: calc(var(--fz) + 8px); color: var(--black); }

h1 { font-family: var(--osc-bold); font-weight: 700; }

h2 { font-size: var(--fz); }

h3 { font-size: calc(var(--fz) - 1px); }

sub, sup { font-size: 60%; }

input { outline: none; }

strong { font-weight: 500; }

.page-wrapper { display: flex; flex-direction: column; height: auto; min-height: 100%; justify-content: space-between; width: 100%; max-width: var(--xxl); margin: 0 auto; }

@media (max-width: 1400px) { .page-wrapper { max-width: var(--xl); } }

@media (max-width: 1200px) { .page-wrapper { max-width: var(--l); } }

@media (max-width: 992px) { .page-wrapper { max-width: var(--m); } }

@media (max-width: 768px) { .page-wrapper { max-width: var(--s); padding: 0 8px; box-sizing: border-box; } }

.input-holder { position: relative; display: inline-block; vertical-align: top; }

.input-holder label { cursor: pointer; display: inline-block; color: var(--grey-black); text-transform: uppercase; background: #fff; padding: 2px 4px; top: 0; left: 8px; transform: translateY(-50%); background: var(--white); font-size: calc(var(--fz) - 4px); line-height: calc(var(--fz) - 2px); position: absolute; z-index: 1; }

#main { box-sizing: border-box; padding: 16px 0 0; flex-grow: 1; }

.card-filter { margin: 0 0 12px; }

.card-filter .buttons-holder .btn { margin: 0 8px 16px 0; }

.card-holder { margin: 0 0 20px; column-count: 4; column-gap: 8px; }

@media (max-width: 1400px) { .card-holder { column-count: 3; } }

@media (max-width: 1200px) { .card-holder { column-count: 2; } }

@media (max-width: 768px) { .card-holder { column-count: 1; } }

.cards-table-grid .card-holder { column-count: 1; column-gap: 0; }

.cards-table-grid .card-holder > div:nth-child(even) { background: none; }

.cards-table-heading { text-transform: uppercase; color: var(--grey-dark); font-size: calc(var(--fz) - 3px); display: none; }

@media (max-width: 992px) { .cards-table-heading { display: none !important; } }

.cards-table-grid .cards-table-heading { display: table; text-align: left; width: 100%; }

.cards-table-heading td { padding: 0 5px 12px 0; box-sizing: border-box; }

.cards-table-heading td:first-child { width: 478px; padding: 0 0 0 135px; }

.cards-table-heading td:nth-child(2) { width: 52px; }

.cards-table-heading td:nth-child(3) { width: 62px; }

.cards-table-heading td:nth-child(4) { width: 116px; }

.cards-table-heading td:nth-child(5) { width: 82px; }

.cards-table-heading td:last-child { text-align: right; }

.btn-icon { width: 24px; height: 24px; position: relative; cursor: pointer; display: inline-block; padding: 0; background: none; border: none; border: none; outline: none; }

.btn-icon svg { width: 24px; height: 24px; }

.btn-icon:hover { color: var(--blacked); }

.btn-icon.btn-search { padding: 0; border: none; background: none; width: 24px; height: 24px; position: absolute; right: 6px; top: 50%; transform: translateY(-50%); }

.app-holder .app-heading { display: flex; align-items: center; justify-content: space-between; margin: 0 0 8px; }

.app-holder .app-heading h1 { margin: 0 0 5px; }

.app-holder .app-heading .btn { color: var(--ok); }

.card-status { color: #fff; border-radius: 2px; font-size: 8px; text-align: center; padding: 4px; }

.card-status.active { background-color: var(--ok); }

.card-status.error { background-color: var(--danger); }

.card-status.stopped { background-color: var(--caution); color: var(--grey-black); }

.app-frame { background: #eef5ff; border-radius: 8px; padding: 16px 8px; box-sizing: border-box; display: flex; justify-content: space-between; width: 100%; }

@media (max-width: 992px) { .app-frame { display: block; } }

.app-log-header { width: 100%; margin: 0 0 6px; display: flex; align-items: flex-start; }

.app-log-header > *:not(.menu-holder) { margin: 0 8px 0 0; }

.app-log-header .menu-holder { margin-left: auto; position: relative; }

.app-log { width: 400px; flex-shrink: 0; }

@media (max-width: 992px) { .app-log { width: 100%; } }

.app-log .grid-2rows { width: 82%; margin: 0 0 8px; }

.app-log .tab-content { padding: 16px 0 0; }

.app-log .btn-list { padding: 16px 12px; display: flex; align-items: center; justify-content: flex-start; }

.app-log .btn-list li:not(:last-child) { margin: 0 20px 0 0; }

.app-log .btn-list li:last-child { margin-left: auto; }

.app-settings { flex-grow: 1; margin: 0 0 0 15px; position: relative; }

@media (max-width: 992px) { .app-settings { margin: 0; } }

.app-settings .btn-info { z-index: 1; right: 0; top: 2px; position: absolute; }

@media (max-width: 1200px) { .app-settings .btn-info { top: -1px; } }

.app-settings .tabs { margin: 0; }

.app-settings .tabs li.active button { background: var(--white); }

@media (max-width: 1200px) { .app-settings .tabs li { margin-right: 2px; }
  .app-settings .tabs button { font-size: calc(var(--fz) - 4px); } }

@media (max-width: 768px) { .app-settings .tabs button { font-size: calc(var(--fz) - 4.5px); } }

.app-settings .tab-content { background: var(--white); border: 1px solid rgba(0, 0, 0, 0.25); box-sizing: border-box; border-radius: 0px 0px 4px 4px; padding: 16px; }

.app-settings .tab-content .input-holder { display: block; margin: 0 0 24px; }

.app-settings .tab-content .input { width: 100%; }

.btn-app-holder { font-size: 0; }

.btn-app-holder > * { display: inline-block; vertical-align: top; margin: 0 4px 0 0; }

.btn-app-holder .btn-frame { position: relative; }

.btn-app-holder .btn-frame .btn { color: var(--white); }

.column-2 { display: grid; gap: 0 24px; grid-template-columns: 1fr 1fr; grid-template-rows: 1fr 1fr; grid-auto-flow: row; }

.column-2 > * { width: 100%; }

.grid-2rows { display: grid; gap: 8px 15px; font-size: calc(var(--fz) - 4px); grid-template-columns: 1fr 1fr 1fr; grid-template-rows: auto 1fr; grid-auto-columns: 1fr 1fr 1fr; grid-auto-rows: 1fr 1fr; grid-auto-flow: column; font-weight: 600; }

.grid-2rows .speed-ok { color: var(--ok); }

.grid-2rows .speed-bad { color: var(--r-premium); }

.grid-2rows > li[class^="speed"] img { display: block; }

.grid-2rows.parameters { text-align: center; gap: 2px 15px; font-weight: 700; color: var(--blacked); }

.grid-2rows.parameters li:nth-child(odd) { font-weight: 300; }

.border-box { border: 1px solid var(--grey-dark); margin: 0 0 24px; box-sizing: border-box; padding: 16px; border-radius: 8px; }

.border-box .btn-icon.h32 { height: 32px; width: 32px; border: 2px solid var(--action); box-sizing: border-box; border-radius: 4px; color: var(--action); }

.flex-holder { display: flex; align-items: center; justify-content: space-between; }

.flex-holder .w120 { max-width: 120px; }

.flex-holder.h32-box > * { height: 32px; }

.flex-holder > *:not(:last-child) { margin: 0 24px 0 0; }

.flex-holder .btn-icon { display: flex; flex-shrink: 0; }

.flex-holder .btn-icon svg { margin: auto; }

.event-list li { padding: 8px 0; border-bottom: 1px solid var(--grey-light); display: flex; align-items: center; justify-content: space-between; }

.event-list li strong { width: 72px; flex-shrink: 0; }

.event-list li p { flex-grow: 1; }

.event-list li .btn-icon-holder { font-size: 0; }

.event-list li .btn-icon-holder .btn-icon { color: var(--grey-dark); display: inline-block; vertical-align: middle; margin: 0 0 0 8px; }

.event-list li .btn-icon-holder .btn-icon:first-child { margin: 0; }
